import setSpacing from '../commands/setSpacing';
import setInd from '../commands/setInd';
import { isSetParagraphSpacing, isSetParagraphInd } from '../actions';
import type { Action, Controller } from '@ali/4ever-cangjie';

export default function onAction(action: Action, controller: Controller, next) {
  /**
   * 若是其他插件中判断，判断 action 时调用的方式如下
   * 
   * const isSetParagraphSpacing = controller.actionPredicator('isSetParagraphSpacing');
   * isSetParagraphSpacing?.(action)
   */


  if (isSetParagraphSpacing(action)) {
    return setSpacing(controller, action.payload);
  }
  if (isSetParagraphInd(action)) {
    return setInd(controller, action.payload);
  }
  return next();
}
